<template>
    <div class="row text-center py-2 text-info" v-if="show.ispay==='0'">
        <div class="col">
            <div class="border  border-info  rounded p-1" @click="$tourl('user_mobile')"><span class="bi bi-tablet-fill me-1"></span>改手机号</div>
        </div>
        <div class="col">
            <div class="border border-info rounded p-1" @click="$tourl('consult')"><span class="bi bi-bookmark-x-fill me-1"></span>客服中心</div>
        </div>
        <div class="col">
            <com-onselect v-if="info[binding].cancel.includes(show.state)" class="border  border-info  rounded p-1"  :type="'cancel-trip'"   :data="setdata(show.id)" @onsubmit="onsubmit"><span class="bi bi-bookmark-x-fill me-1"></span>取消订单</com-onselect>
            <div v-else class="border rounded bg-light text-black-50 p-1"><span class="bi bi-bookmark-x-fill me-1"></span>取消订单</div>
        </div>
    </div>
</template>

<script>
export default {
  props: {
    binding:'',
    set:{},
    data:{},
    show:{},
  },
  data(){
    return {
      info:{
        "p":{
          "cancel":['1','2','22','31','32','33'],
          "cancel_data":{url:'/passenger/state',data:{id:this.show.id,state:'8'}},
        },
        "d":{
          "cancel":['2','22','31','32','33'],
          "cancel_data":{url:'/driver/state',data:{id:this.show.id,state:'7'}},
        },
      }
    }
  },
  methods:{
    onsubmit(key){
       this.$emit('onsubmit',key);
    },
    setdata(id){
      return {binding:this.binding,cancel:this.$store.state.userinfo.cancel_int,dcancel:this.$store.state.userinfo.dcancel_int,form:this.info[this.binding].cancel_data}
    }
  }
}
</script>

<style>

</style>


